.dashboardmain{
  .header {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    overflow: hidden;
  }
  .navbar {
    width: 100%;
    height: 100px;
    position: fixed;
    top: 0;
    background: #1f2235;
    z-index: 1;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  }
  
  .navbar__container {
    display: flex;
    height: 100px;
    align-items: center;
    justify-content: space-between;
  }
  .navbar__left-logo img {
    width: 110px;
  }
  
  .navbar__right {
    display: flex;
    list-style-type: none;
  }
  .navbar__right li a {
    text-decoration: none;
    color: #fff;
    font-weight: 500;
    font-size: 18px;
    padding: 0 15px;
  }
  
  /* Helpers */
  .container {
    width: 1200px;
    margin: 0 auto;
  }
  .bgMain {
    background: #1f2235;
  }
  
  .h-650 {
    height: 650px;
  }
  .alignCenter {
    align-items: center;
  }
  .justifyConter {
    justify-content: center;
  }
  
  
  
  /* header */
  .header__content {
    display: flex;
    align-items: center;
    height: 100vh;
    width: 100%;
    color: #fff;
  }
  .header__ul {
    list-style-type: none;
    display: flex;
    margin-bottom: 20px;
  }
  .header__ul li {
    width: 50px;
    height: 50px;
    border: 2px solid #ff4a57;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 10px;
    transition: all 0.5s linear;
    cursor: pointer;
  }
  .header__ul li:hover {
    background: #fff;
    border: 2px solid transparent;
  }
  .header__ul li:hover .headerIcon {
    color: #ff4a57;
  }
  .header__ul li:first-child {
    margin-left: 0;
  }
  .header__section h1 {
    font-size: 57px;
    font-weight: 700;
    margin-bottom: 20px;
  }
  .header__section p {
    font-size: 26px;
    font-weight: 300;
    line-height: 40px;
    margin-bottom: 50px;
  }
  
  .btn {
    outline: none;
    border: none;
    color: #fff;
    text-decoration: none;
  }
  .btn-outline {
    border: 2px solid #ff4a57;
    border-radius: 50px;
    padding: 13px 40px;
  }
  .btn-smart {
    background: #ff4a57;
    padding: 13px 40px;
    border-radius: 50px;
  }
  .play {
    font-size: 25px !important;
    padding-top: 10px;
  }
  .banner__img {
    padding: 0 30px;
    margin-top: 150px;
  }
  
  /* Servives */
  .services {
    background: #1f2235;
    padding: 100px 0;
    color: #fff;
  }
  
  .common {
    width: 33.33333333333333%;
    margin: 20px auto;
    text-align: center;
  }
  
  .heading {
    margin-bottom: 20px;
    font-weight: 500;
    color: #ff4a57;
    font-size: 16px;
  }
  .mainHeader {
    font-size: 32px;
    margin-bottom: 20px;
    color: #fff;
  }
  .mainContent {
    font-size: 16px;
    color: #dfdfdf;
  }
  .commonBorder {
    background: #ff4a57;
    height: 3px;
    width: 100px;
    margin: 30px auto;
  }
  
  .services__box {
    padding: 40px;
    background: #23263a;
    color: #fff;
    margin: 20px;
    transition: all 0.5s linear;
  }
  .services__box:hover {
    background: #ff4a57;
  }
  .commonIcons {
    font-size: 40px;
    color: #ff4a57;
    margin-bottom: 15px;
  }
  .services__box:hover .commonIcons {
    color: #fff;
  }
  .services__box-header {
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 15px;
  }
  .services__box-p {
    font-size: 14px;
    color: #dfdfdf;
  }
  .about {
    width: 100%;
    background: #23263a;
    padding: 100px 0;
  }
  .about__img {
    padding: 30px;
  }
  .about__img img {
    width: 300px;
    height: 400px;
  }
  .about__info {
    color: #fff;
  }
  .about__info h1 {
    margin-bottom: 30px;
  }
  .about__info-p1 {
    font-size: 14px;
    margin-bottom: 20px;
  }
  .about__info-p2 {
    font-size: 14px;
  }
  
  .info__contacts {
    margin-top: 20px;
  }
  .info__contacts strong {
    color: #ff4a57;
  }
  .info__contacts p {
    margin-top: 3px;
  }
  .info__contacts .col-6 {
    margin: 10px 0;
  }
  
  
  .toggle {
    position: fixed;
    top: 20px;
    right: 25px;
    background: #000;
    color: #fff;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    display: none;
  }
  @media (max-width: 765px) {
    .container {
      width: 100%;
      padding: 0 50px;
    }
    .col-6 {
      width: 100%;
    }
    .col-3 {
      width: 100%;
    }
    .col-4 {
      width: 100%;
    }
    .navbar__right {
      flex-direction: column;
      width: 100%;
      align-items: center;
      background: #1f2235;
      position: fixed;
      top: 0;
      left: 0;
    }
    .navbar__right li a {
      display: block;
      padding: 20px 0;
      font-size: 14px;
    }
    .toggle {
      display: flex;
    }
    .banner__img img {
      display: none;
    }
    .header__section h1 {
      font-size: 35px;
      font-weight: 700;
      margin-bottom: 20px;
    }
    .header__section p {
      font-size: 20px;
      font-weight: 300;
      line-height: 30px;
      margin-bottom: 50px;
    }
    .common {
      width: 100%;
    }
    .h-650 {
      height: auto;
    }
  }
  
}
